/**
 *  - 
 * 
 */

import React from 'react';
import styled from 'styled-components';
import { Badge } from '../../ui/components/Badge/Badge';
import { IconButton } from '../../ui/components/IconButton/IconButton';
import { Progress } from '../../ui/components/Progress/Progress';

const StatusBarContainer = styled.div`
  height: 24px;
  background: ${({ theme }) => theme.colors.surface};
  border-top: 1px solid ${({ theme }) => theme.colors.border.default};
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 ${({ theme }) => theme.spacing.md};
  font-size: ${({ theme }) => theme.typography.fontSize.xs};
  color: ${({ theme }) => theme.colors.text.secondary};
`;

const StatusSection = styled.div`
  display: flex;
  align-items: center;
  gap: ${({ theme }) => theme.spacing.md};
`;

const StatusItem = styled.div`
  display: flex;
  align-items: center;
  gap: ${({ theme }) => theme.spacing.xs};
`;

const PerformanceIndicator = styled.div<{ $level: 'good' | 'warning' | 'error' }>`
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: ${({ theme, $level }) => {
    switch ($level) {
      case 'good': return theme.colors.success;
      case 'warning': return theme.colors.warning;
      case 'error': return theme.colors.error;
      default: return theme.colors.text.disabled;
    }
  }};
`;

const Divider = styled.div`
  width: 1px;
  height: 16px;
  background: ${({ theme }) => theme.colors.border.default};
`;

const ProgressContainer = styled.div`
  width: 120px;
  display: flex;
  align-items: center;
  gap: ${({ theme }) => theme.spacing.xs};
`;

export const StatusBar: React.FC = () => {
  // 
  const performanceData = {
    memory: 85, // MB
    fps: 60,
    cpu: 15, // %
  };

  const getPerformanceLevel = (value: number, thresholds: [number, number]) => {
    if (value <= thresholds[0]) return 'good';
    if (value <= thresholds[1]) return 'warning';
    return 'error';
  };

  const memoryLevel = getPerformanceLevel(performanceData.memory, [100, 200]);
  const fpsLevel = performanceData.fps >= 55 ? 'good' : performanceData.fps >= 30 ? 'warning' : 'error';
  const cpuLevel = getPerformanceLevel(performanceData.cpu, [30, 60]);

  return (
    <StatusBarContainer>
      {/*  */}
      <StatusSection>
        <StatusItem>
          <Badge variant="success" size="sm"></Badge>
          <span></span>
        </StatusItem>

        <Divider />

        <StatusItem>
          <span>: 800600</span>
        </StatusItem>

        <StatusItem>
          <span>: 100%</span>
        </StatusItem>

        <Divider />

        <StatusItem>
          <span>: 3</span>
        </StatusItem>

        <StatusItem>
          <span>: 1</span>
        </StatusItem>
      </StatusSection>

      {/*  */}
      <StatusSection>
        {/*  */}
        {false && (
          <>
            <ProgressContainer>
              <Progress value={65} size="sm" />
              <span>...</span>
            </ProgressContainer>
            <Divider />
          </>
        )}

        {/*  */}
        <StatusItem>
          <PerformanceIndicator $level={memoryLevel} />
          <span>: {performanceData.memory}MB</span>
        </StatusItem>

        <StatusItem>
          <PerformanceIndicator $level={fpsLevel} />
          <span>FPS: {performanceData.fps}</span>
        </StatusItem>

        <StatusItem>
          <PerformanceIndicator $level={cpuLevel} />
          <span>CPU: {performanceData.cpu}%</span>
        </StatusItem>

        <Divider />

        {/*  */}
        <StatusItem>
          <span>Ctrl+Z  | Ctrl+Y  | V </span>
        </StatusItem>

        <Divider />

        {/*  */}
        <StatusItem>
          <PerformanceIndicator $level="good" />
          <span></span>
        </StatusItem>

        {/*  */}
        <IconButton
          variant="ghost"
          size="xs"
          icon=""
          onClick={() => console.log('Toggle theme')}
        />
      </StatusSection>
    </StatusBarContainer>
  );
};