/**
 * 
 * 
 */

export interface InitializationState {
  status: 'idle' | 'initializing' | 'completed' | 'failed';
  startTime: number | null;
  completedTime: number | null;
  error: Error | null;
  retryCount: number;
}

export interface InitializationOptions {
  maxRetries?: number;
  timeout?: number;
  enableLogging?: boolean;
}

/**
 * 
 * 
 */
export class InitializationManager {
  private static instance: InitializationManager | null = null;
  private state: InitializationState;
  private options: Required<InitializationOptions>;
  private initializationPromise: Promise<void> | null = null;
  private timeoutId: NodeJS.Timeout | null = null;

  private constructor(options: InitializationOptions = {}) {
    this.options = {
      maxRetries: options.maxRetries ?? 3,
      timeout: options.timeout ?? 10000, // 10
      enableLogging: options.enableLogging ?? true,
    };

    this.state = {
      status: 'idle',
      startTime: null,
      completedTime: null,
      error: null,
      retryCount: 0,
    };
  }

  /**
   * 
   */
  public static getInstance(options?: InitializationOptions): InitializationManager {
    if (!InitializationManager.instance) {
      InitializationManager.instance = new InitializationManager(options);
    }
    return InitializationManager.instance;
  }

  /**
   * 
   */
  public getState(): InitializationState {
    return { ...this.state };
  }

  /**
   * 
   */
  public get isInitialized(): boolean {
    return this.state.status === 'completed';
  }

  /**
   * 
   */
  public get isInitializing(): boolean {
    return this.state.status === 'initializing';
  }

  /**
   * 
   */
  public get hasFailed(): boolean {
    return this.state.status === 'failed';
  }

  /**
   * 
   * Promise
   */
  public async initializeOnce(initFunction: () => Promise<void>): Promise<void> {
    // 
    if (this.state.status === 'completed') {
      this.log('');
      return;
    }

    // Promise
    if (this.state.status === 'initializing' && this.initializationPromise) {
      this.log('...');
      return this.initializationPromise;
    }

    // 
    if (this.state.status === 'failed') {
      if (this.state.retryCount >= this.options.maxRetries) {
        throw new Error(` (${this.options.maxRetries})`);
      }
      this.log(` (${this.state.retryCount + 1}/${this.options.maxRetries})`);
    }

    // 
    this.initializationPromise = this.performInitialization(initFunction);
    return this.initializationPromise;
  }

  /**
   * 
   */
  private async performInitialization(initFunction: () => Promise<void>): Promise<void> {
    try {
      // 
      this.updateState({
        status: 'initializing',
        startTime: Date.now(),
        error: null,
      });

      this.log('...');

      // 
      this.setupTimeout();

      // 
      await initFunction();

      // 
      this.clearTimeout();

      // 
      this.updateState({
        status: 'completed',
        completedTime: Date.now(),
      });

      const duration = this.state.completedTime! - this.state.startTime!;
      this.log(`: ${duration}ms`);

    } catch (error) {
      // 
      this.clearTimeout();

      // 
      this.state.retryCount++;

      // 
      this.updateState({
        status: 'failed',
        error: error instanceof Error ? error : new Error(String(error)),
      });

      this.log(`: ${error instanceof Error ? error.message : String(error)}`);

      // 
      throw error;
    }
  }

  /**
   * 
   */
  private setupTimeout(): void {
    this.timeoutId = setTimeout(() => {
      const error = new Error(` (${this.options.timeout}ms)`);
      this.updateState({
        status: 'failed',
        error,
      });
      this.log(`: ${this.options.timeout}ms`);
    }, this.options.timeout);
  }

  /**
   * 
   */
  private clearTimeout(): void {
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
      this.timeoutId = null;
    }
  }

  /**
   * 
   */
  private updateState(updates: Partial<InitializationState>): void {
    this.state = { ...this.state, ...updates };
  }

  /**
   * 
   */
  public reset(): void {
    this.clearTimeout();
    this.initializationPromise = null;
    this.state = {
      status: 'idle',
      startTime: null,
      completedTime: null,
      error: null,
      retryCount: 0,
    };
    this.log('');
  }

  /**
   * 
   */
  public async forceReinitialize(initFunction: () => Promise<void>): Promise<void> {
    this.log('...');
    this.reset();
    return this.initializeOnce(initFunction);
  }

  /**
   * 
   */
  public getStats(): {
    isInitialized: boolean;
    isInitializing: boolean;
    hasFailed: boolean;
    duration: number | null;
    retryCount: number;
    error: string | null;
  } {
    const duration = this.state.startTime && this.state.completedTime
      ? this.state.completedTime - this.state.startTime
      : null;

    return {
      isInitialized: this.isInitialized,
      isInitializing: this.isInitializing,
      hasFailed: this.hasFailed,
      duration,
      retryCount: this.state.retryCount,
      error: this.state.error?.message || null,
    };
  }

  /**
   * 
   */
  private log(message: string): void {
    if (this.options.enableLogging) {
      console.log(`[InitializationManager] ${message}`);
    }
  }

  /**
   * 
   */
  public destroy(): void {
    this.clearTimeout();
    this.initializationPromise = null;
    InitializationManager.instance = null;
  }
}

// 
export const initializationManager = InitializationManager.getInstance();