/**
 *  - useEffect
 * 
 */

import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import styled from 'styled-components';
import { ThemeProvider } from '../../ui/theme/ThemeProvider';
import { GlobalStyles } from '../../ui/styles/GlobalStyles';
import { MainLayout } from '../Layout/MainLayout';
import { WelcomeScreen } from '../Welcome/WelcomeScreen';
import { useAppStore } from '../../stores/appStore';
import { useAppInitialization } from '../../hooks/useAppInitialization';
import { useRenderCounter } from '../../hooks/useRenderCounter';
import { reactLoopFixToolkit } from '../../utils/ReactLoopFix';

//  Hook - useCallback
const useWindowControl = () => {
  const originalSizeRef = useRef<{ width: number; height: number } | null>(null);

  const setWelcomeMode = useCallback(async () => {
    try {
      reactLoopFixToolkit.debugLogger.debug(
        'window-control',
        '',
        {},
        'useWindowControl'
      );

      // 
      const sizeResult = await window.electronAPI.window.getSize();
      if (sizeResult.success && sizeResult.data) {
        originalSizeRef.current = sizeResult.data;
      }

      //  (600x450)
      await window.electronAPI.window.setSize(600, 450, true);
      await window.electronAPI.window.setResizable(false);
      await window.electronAPI.window.center();
      
      reactLoopFixToolkit.debugLogger.info(
        'window-control',
        ': 600x450, ',
        { width: 600, height: 450 },
        'useWindowControl'
      );
    } catch (error) {
      reactLoopFixToolkit.debugLogger.error(
        'window-control',
        '',
        { error: error instanceof Error ? error.message : String(error) },
        'useWindowControl'
      );
    }
  }, []);

  const restoreNormalMode = useCallback(async () => {
    try {
      reactLoopFixToolkit.debugLogger.debug(
        'window-control',
        '',
        {},
        'useWindowControl'
      );

      // 
      await window.electronAPI.window.setResizable(true);
      
      // 
      if (originalSizeRef.current) {
        await window.electronAPI.window.setSize(
          originalSizeRef.current.width,
          originalSizeRef.current.height,
          true
        );
      } else {
        // 
        await window.electronAPI.window.setSize(1200, 800, true);
      }
      
      await window.electronAPI.window.center();
      
      reactLoopFixToolkit.debugLogger.info(
        'window-control',
        '',
        { 
          width: originalSizeRef.current?.width || 1200, 
          height: originalSizeRef.current?.height || 800 
        },
        'useWindowControl'
      );
    } catch (error) {
      reactLoopFixToolkit.debugLogger.error(
        'window-control',
        '',
        { error: error instanceof Error ? error.message : String(error) },
        'useWindowControl'
      );
    }
  }, []);

  return useMemo(() => ({ setWelcomeMode, restoreNormalMode }), [setWelcomeMode, restoreNormalMode]);
};


const AppWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: ${({ theme }) => theme.colors.background};
`;

const LoadingScreen = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: ${({ theme }) => theme.colors.background};
  color: ${({ theme }) => theme.colors.text};
  font-size: 16px;
`;

const ErrorScreen = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: ${({ theme }) => theme.colors.background};
  color: ${({ theme }) => theme.colors.error || '#ff6b6b'};
  text-align: center;
  padding: 20px;

  h2 {
    margin-bottom: 16px;
    font-size: 24px;
  }

  p {
    margin-bottom: 16px;
    font-size: 14px;
    opacity: 0.8;
  }
`;

const RetryButton = styled.button`
  padding: 8px 16px;
  background: ${({ theme }) => theme.colors.primary || '#3b82f6'};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  
  &:hover {
    opacity: 0.9;
  }

  &:active {
    transform: translateY(1px);
  }
`;

export const AppContainer: React.FC = () => {
  // storeinitializeApp
  const { isFirstTime } = useAppStore();
  
  // 
  const [showWelcome, setShowWelcome] = useState(true);
  const [hasSetWelcomeMode, setHasSetWelcomeMode] = useState(false);
  
  // 
  const { setWelcomeMode, restoreNormalMode } = useWindowControl();
  
  // Hook
  const {
    isInitialized,
    isInitializing,
    initializationError,
    hasError,
    manualInit,
  } = useAppInitialization({
    enableAutoInit: true,
    onInitialized: useCallback(() => {
      reactLoopFixToolkit.debugLogger.info(
        'app-container',
        '',
        { isFirstTime },
        'AppContainer'
      );
    }, [isFirstTime]),
    onError: useCallback((error: Error) => {
      reactLoopFixToolkit.debugLogger.error(
        'app-container',
        '',
        { error: error.message },
        'AppContainer'
      );
    }, []),
  });

  // 
  const handleWelcomeComplete = useCallback(() => {
    reactLoopFixToolkit.debugLogger.info(
      'app-container',
      '',
      {},
      'AppContainer'
    );

    setShowWelcome(false);
    restoreNormalMode();
  }, [restoreNormalMode]);

  // 
  const handleRetryInit = useCallback(async () => {
    try {
      reactLoopFixToolkit.debugLogger.info(
        'app-container',
        '',
        {},
        'AppContainer'
      );
      await manualInit();
    } catch (error) {
      reactLoopFixToolkit.debugLogger.error(
        'app-container',
        '',
        { error: error instanceof Error ? error.message : String(error) },
        'AppContainer'
      );
    }
  }, [manualInit]);

  //  - 
  useEffect(() => {
    if (isInitialized && isFirstTime && !hasSetWelcomeMode) {
      reactLoopFixToolkit.debugLogger.debug(
        'app-container',
        '',
        { isFirstTime, hasSetWelcomeMode },
        'AppContainer'
      );

      setWelcomeMode();
      setHasSetWelcomeMode(true);
      setShowWelcome(true);
    } else if (isInitialized && !isFirstTime) {
      reactLoopFixToolkit.debugLogger.debug(
        'app-container',
        '',
        { isFirstTime },
        'AppContainer'
      );

      setShowWelcome(false);
    }
  }, [isInitialized, isFirstTime, hasSetWelcomeMode, setWelcomeMode]);

  //  - useMemo
  const renderState = useMemo(() => {
    if (hasError) {
      return 'error';
    }
    if (isInitializing) {
      return 'loading';
    }
    if (!isInitialized) {
      return 'loading';
    }
    if (showWelcome && isFirstTime) {
      return 'welcome';
    }
    return 'main';
  }, [hasError, isInitializing, isInitialized, showWelcome, isFirstTime]);

  // Hook
  useRenderCounter(
    {
      componentName: 'AppContainer',
      enableLogging: process.env['NODE_ENV'] === 'development',
      logProps: true,
      maxRenderWarning: 15,
    },
    { renderState, isInitialized, isFirstTime, showWelcome },
    `render state: ${renderState}`
  );

  // 
  const renderContent = () => {
    switch (renderState) {
      case 'error':
        return (
          <ErrorScreen>
            <h2></h2>
            <p>{initializationError}</p>
            <RetryButton onClick={handleRetryInit}>
              
            </RetryButton>
          </ErrorScreen>
        );

      case 'loading':
        return (
          <LoadingScreen>
            ...
          </LoadingScreen>
        );

      case 'welcome':
        return <WelcomeScreen onComplete={handleWelcomeComplete} />;

      case 'main':
        return <MainLayout />;

      default:
        return (
          <LoadingScreen>
            ...
          </LoadingScreen>
        );
    }
  };

  return (
    <ThemeProvider>
      <GlobalStyles />
      <AppWrapper>
        {renderContent()}
      </AppWrapper>
    </ThemeProvider>
  );
};
