/**
 *  - Figma UI3
 * 
 */

import { colors, spacing, typography, borderRadius, shadows, animation, zIndex } from './tokens';

// 
export interface Theme {
  colors: {
    // 
    primary: string;
    secondary: string;
    accent: string;

    // 
    success: string;
    warning: string;
    error: string;
    info: string;

    // 
    background: string;
    surface: string;
    overlay: string;

    // 
    text: {
      primary: string;
      secondary: string;
      tertiary: string;
      disabled: string;
      inverse: string;
    };

    // 
    border: {
      default: string;
      focus: string;
      hover: string;
    };

    // 
    shadow: {
      small: string;
      medium: string;
      large: string;
    };

    // 
    canvas: {
      background: string;
      grid: string;
      selection: string;
      selectionBg: string;
      guide: string;
    };
  };
  spacing: {
    xs: string;
    sm: string;
    md: string;
    lg: string;
    xl: string;
    '2xl': string;
    '3xl': string;
  };
  typography: {
    fontFamily: {
      primary: string;
      mono: string;
    };
    fontSize: typeof typography.fontSize;
    fontWeight: typeof typography.fontWeight;
    lineHeight: typeof typography.lineHeight;
  };
  borderRadius: typeof borderRadius;
  shadows: typeof shadows;
  animation: typeof animation;
  zIndex: typeof zIndex;
}

//  - 
export const lightTheme: Theme = {
  colors: {
    primary: colors.primary[500],
    secondary: colors.primary[600],
    accent: colors.primary[400],
    success: colors.semantic.success[500],
    warning: colors.semantic.warning[500],
    error: colors.semantic.error[500],
    info: colors.semantic.info[500],
    background: '#fafbfc', // 
    surface: '#ffffff', // 
    overlay: 'rgba(15, 23, 42, 0.6)', // 
    text: {
      primary: colors.neutral[900],
      secondary: colors.neutral[700], // 
      tertiary: colors.neutral[500],
      disabled: colors.neutral[400],
      inverse: colors.neutral[0],
    },
    border: {
      default: colors.neutral[200],
      focus: colors.primary[500],
      hover: colors.neutral[300],
    },
    shadow: {
      small: '0 1px 2px 0 rgba(0, 0, 0, 0.05)',
      medium: '0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)',
      large: '0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)',
    },
    canvas: {
      ...colors.canvas,
      background: '#f8fafc', // 
    },
  },
  spacing: {
    xs: spacing[1],
    sm: spacing[2],
    md: spacing[4],
    lg: spacing[6],
    xl: spacing[8],
    '2xl': spacing[12],
    '3xl': spacing[16],
  },
  typography: {
    fontFamily: {
      primary: typography.fontFamily.sans,
      mono: typography.fontFamily.mono,
    },
    fontSize: typography.fontSize,
    fontWeight: typography.fontWeight,
    lineHeight: typography.lineHeight,
  },
  borderRadius,
  shadows,
  animation,
  zIndex,
};

//  - 
export const darkTheme: Theme = {
  ...lightTheme,
  colors: {
    ...lightTheme.colors,
    background: '#0f172a', // 
    surface: '#1e293b', // 
    overlay: 'rgba(0, 0, 0, 0.8)', // 
    text: {
      primary: '#f8fafc', // 
      secondary: '#cbd5e1', // 
      tertiary: '#94a3b8',
      disabled: '#64748b',
      inverse: '#0f172a',
    },
    border: {
      default: '#334155', // 
      focus: colors.primary[400], // 
      hover: '#475569',
    },
    shadow: {
      small: '0 1px 2px 0 rgba(0, 0, 0, 0.3)',
      medium: '0 4px 6px -1px rgba(0, 0, 0, 0.4), 0 2px 4px -1px rgba(0, 0, 0, 0.3)',
      large: '0 20px 25px -5px rgba(0, 0, 0, 0.5), 0 10px 10px -5px rgba(0, 0, 0, 0.4)',
    },
    canvas: {
      background: '#1e293b',
      grid: '#334155',
      selection: '#6366f1',
      selectionBg: 'rgba(99, 102, 241, 0.15)', // 
      guide: '#f59e0b',
    },
  },
};

// 
export const theme = lightTheme;
export default theme;

// 
export type ThemeColors = Theme['colors'];
export type ThemeSpacing = Theme['spacing'];

// 
export * from './tokens';
export * from './ThemeProvider';